    column-gap: 1em;

  }
  /*一个内容层*/
  .item{
    padding: 1em;
    margin: 0 0 1em 0;
    -moz-page-break-inside: avoid;
    -webkit-column-break-inside: avoid;
    break-inside: avoid;
    border: 1px solid #000;
    background: white;
  }
  .item img{
    width: 100%;
    margin-bottom:10px;
  }
</style>
