  import '../../static/imgs'
  import son from '../components/son.vue'
  export default {
    name: '',
    data () {
      return {
        msg:true,
        ri:true,
        imgs:window.imgs
      }
    }, created(){

    }, mounted(){

    }, methods: {}
    ,components:{

    }
  }
</script>



